import React from 'react';
import './App.css';
import 'bootstrap';
import {Row, Col, Container} from 'react-bootstrap';

import d20_calc_img from './d20 attack comparison.jpg';
import sp_timer_img from './anchorman.jpg';
import keyfinder_img from './keyfinder.jpg';


const D20_CALC_TITLE="D20 Attack Comparison"
const D20_CALC_LINK="https://kccpmg.github.io/dnd-attack-comparison"
const D20_CALC_IMG=d20_calc_img;
const D20_CALC_DESCRIPTION=(
  <div>
    During the pandemic, my group of friends decided to start playing Dungeons and Dragons, with half of us being first-time players. As the game grew in complexity and more gameplay options became available to us, I took it upon myself to help us visualize the varying efficiency of different things that we could be doing during combat. To that end, I created this Attack Calculator.<br/><br/>
  
    To use it, click the button to "Add Character" and create a Character with whatever name you like. Once the character is created, the "Add Attack" button can be clicked to bring up the Attack modal to specify the chance to hit, as well as the damage of each attack done in a turn. Once the attack is created, it can be edited, or it can be displayed on the graph below. The graph shows the expected average damage of an attack against a range of Armor Classes (enemy defense scores), and attacks can be compared against one another, as well as as against attacks of other characters.
  </div>
)

const SP_TIMER_TITLE="Anchorman: Sex Panther Timer"
const SP_TIMER_LINK="https://kccpmg.github.io/sex-panther-timer"
const SP_TIMER_IMG=sp_timer_img;
const SP_TIMER_DESCRIPTION= (
  <div>
    While visiting my brother before his wedding, he, his fiancée, and I were going to play <a href='https://barryandjason.com/products/anchorman'>Anchorman: The Board Game</a>, which I had given them the preceding Christmas. Unfortunately, the timer which had come with the game was broken. While we could have replaced it with any timer, the one that was included with the game was designed to resemble the iconic bottle of Sex Panther Cologne featured in the film, complete with a panther roar when time was up. I felt as though using a different timer subtracted some of the immersion of the game, so I built this simple app to replace the functionality of the timer, include a panther growl, and add an additional bonus of dropping in one of several dozen soundclips taken from the movie. <b>Please note: This web app will function on Chrome, but may not work on Safari, DuckDuckGo, etc.</b>
  </div>)

const KEYFINDER_TITLE="Keyfinder"
const KEYFINDER_LINK="https://kccpmg.github.io/keyfinder"
const KEYFINDER_IMG=keyfinder_img;
const KEYFINDER_DESCRIPTION=(
  <div>
    While musically experimenting, I wanted to have a better sense of what chords I could play within a given key, and designed this tool to help with that. My goal was to model notes, chords, and keys, with the basic idea that selecting some would give you compatibility with others. <br/><br/>
    To use this app, click on either "Select Notes", "Select Chord", or "Select Key". By selecting notes, you can click on any number of available notes, and the app will then show you which keys and chords contain all of the notes that you selected (though not necessarily only those notes). Likewise, selecting a Key or Chord will give you the opportunity to select the root note and then the type of the Key/Chord. This will then tell you all of the notes that exist in that Key/Chord, and if selecting a Key, will also tell you all of the naturally occuring Chords within that Key. Similarly, if you select a Chord, the app will reveal all of the Keys in which that Chord is naturally occurring.
  </div>
  )



const NONE = "NONE";
const KEYFINDER = "KEYFINDER";
const SP_TIMER = "SP_TIMER";
const D20_CALC = "D20_CALC";



class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      displayedProject: NONE
    }
  }

  setDisplay = (project) => {
    this.setState({displayedProject: project});
  }

  render() {
    return(
      <div>
        <Heading 
          displayedProject={this.state.displayedProject}
        />
        <ProjectView 
          displayedProject={this.state.displayedProject} 
          setDisplay={this.setDisplay}
          style={{
            height: "600px",
          }}
        />
      </div>
    )
  }
}

class Heading extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {

    let bio = (
      <Row className="bio-row">
        {/* <Col> */}
          Hi there, my name is Connor Wales. After a brief flirtation with Computer Science in college, I've made a more sincere effort to return to it in my free time over these past several years. While I originally began with Python, I eventually switched over to Javascript in both front-end and back-end projects, with an eye towards some more ambitions creations that I am actively working on. <br/><br/>
          What follows here are just a few simple projects that I have made with specific applications for myself, friends, and family. The bulk of what I have done to this point is along similar lines, not always having a user interface, but always being something that has some value to my interests and those of the people around me. Thanks for stopping by, and I hope to be able to share more of what I've done with you soon. 
      </Row>
    )

    

    return (
      <Container fluid className="heading">
        <Row>
          <h1>Connor M. Wales</h1>
        </Row>
        {this.props.displayedProject===NONE ? bio : ""}      
      </Container>
    )
  }
}


class ProjectView extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    if (this.props.displayedProject === NONE) return (<ProjectMenu setDisplay={this.props.setDisplay}/>)

    else if (this.props.displayedProject === D20_CALC) return (
      <Project 
        addClass="d20-calc" 
        title={D20_CALC_TITLE} 
        description={D20_CALC_DESCRIPTION} 
        image={D20_CALC_IMG} link={D20_CALC_LINK} 
        setDisplay={this.props.setDisplay}
      />
    )
    else if (this.props.displayedProject === SP_TIMER) return (
      <Project 
        addClass="sp-timer" 
        title={SP_TIMER_TITLE} 
        description={SP_TIMER_DESCRIPTION} 
        image={SP_TIMER_IMG} 
        link={SP_TIMER_LINK} 
        setDisplay={this.props.setDisplay}
      />
    )
    else if (this.props.displayedProject === KEYFINDER) return (
      <Project 
        addClass="keyfinder" 
        title={KEYFINDER_TITLE} 
        description={KEYFINDER_DESCRIPTION} 
        image={KEYFINDER_IMG} 
        link={KEYFINDER_LINK} 
        setDisplay={this.props.setDisplay}
      />
    )
  }
}

class ProjectMenu extends React.Component {
  constructor(props) {
    super(props);
  }

  render(props) {
    return (
      <Container className="project-menu">
        <Row>
          <ClickableProject 
            title={SP_TIMER_TITLE}
            image={SP_TIMER_IMG}
            displayOption={SP_TIMER}
            setDisplay={this.props.setDisplay} 
          />
          <ClickableProject 
            title={D20_CALC_TITLE}
            image={D20_CALC_IMG}
            displayOption={D20_CALC}
            setDisplay={this.props.setDisplay} 
          />
          <ClickableProject 
            title={KEYFINDER_TITLE}
            image={KEYFINDER_IMG}
            displayOption={KEYFINDER}
            setDisplay={this.props.setDisplay} 
          />
        </Row>
      </Container>
    )
  }
}

class ClickableProject extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <Col 
        className="clck-prj-col" xs={6} sm={6} md={4} lg={3}
        onClick={
          ()=>this.props.setDisplay(this.props.displayOption)
        }  
      >
        <h5>{this.props.title}</h5>
        <img src={this.props.image}/>
      </Col>
    )
  }
}

class Project extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      title: this.props.title,
      description: this.props.description,
      image: this.props.image,
      link: this.props.link
    }
  }

  render() {
    return (
      <Container fluid className={`project ${this.props.addClass}`}>
        <div className="dummy" />
        <Row className="project-title">
          <h1>{this.props.title}</h1>
        </Row>
        <Row className="project-detail">
          <Col className="img-col align-items-center" xs={12} sm={6} md={4} lg={3} xl={3}>
            <a href={this.props.link} >
              <Row>
                <img src={this.props.image} alt={this.props.title}/>
              </Row>
            </a>
          </Col>
          <Col xs={12} sm={6} md={8} lg={9} xl={9}>
            {this.props.description}
          </Col>
        </Row>
        <Row className="button-row" text-center> 
          <Col>
            <btn 
              className="menu-button"
              onClick={()=>this.props.setDisplay(NONE)}
            >
              Return to Menu
            </btn>
          </Col>
        </Row>
      </Container>
    )
  }
}

export default App;
